// useLocalStorageCountdown.ts
import { ref, computed, onMounted, onUnmounted } from 'vue'

export type NowFn = () => number

export function useLocalStorageCountdown(opts: {
  key: string // localStorage key，存 endTs
  durationMs: number // 冷却时长
  now?: NowFn // 用你的 getSysTs() 更稳
}) {
  const nowFn = opts.now ?? (() => Date.now())

  const endTs = ref<number>(0)
  const remainingS = ref<number>(0)

  let timer: ReturnType<typeof setTimeout> | undefined

  function clearTimer() {
    if (timer) {
      clearTimeout(timer)
      timer = undefined
    }
  }

  function persistEndTs(ts: number) {
    localStorage.setItem(opts.key, String(ts))
  }

  function removePersist() {
    localStorage.removeItem(opts.key)
  }

  function tick() {
    const now = nowFn()
    // 用 ceil 更符合用户感知（60->59->...->1->0），并且保证不出现 -1
    const s = Math.max(0, Math.ceil((endTs.value - now) / 1000))
    remainingS.value = s

    if (s <= 0) {
      stop() // 到期统一收口
      return
    }

    // 对齐到下一个整秒边界，减少漂移
    const next = 1000 - (now % 1000)
    clearTimer()
    timer = setTimeout(tick, next)
  }

  function loadFromStorage() {
    const raw = localStorage.getItem(opts.key)
    const ts = raw ? Number(raw) : 0
    if (!Number.isFinite(ts) || ts <= 0) {
      stop(false)
      return
    }

    const now = nowFn()
    if (ts > now) {
      endTs.value = ts
      tick()
    } else {
      stop(true)
    }
  }

  // start：默认从当前时间开始 duration
  function start(startTs = nowFn()) {
    endTs.value = startTs + opts.durationMs
    persistEndTs(endTs.value)
    tick()
  }

  // 允许你直接 set endTs（如果你业务里有别处计算过）
  function startWithEndTs(ts: number) {
    endTs.value = ts
    persistEndTs(endTs.value)
    tick()
  }

  // stop(clearStorage=true)：结束并可选清理 localStorage
  function stop(clearStorage = true) {
    clearTimer()
    remainingS.value = 0
    endTs.value = 0
    if (clearStorage) removePersist()
  }

  const running = computed(() => remainingS.value > 0)

  onMounted(loadFromStorage)
  onUnmounted(clearTimer)

  return {
    endTs,
    remainingS,
    running,
    start,
    startWithEndTs,
    stop,
    loadFromStorage,
  }
}
