import { getArgsFromUrl, getPlatform, registerBridgeCbId } from '@badam/common-utils'
import { sleep } from '@d/utils/dial'
import { showModal } from '@@/components/modal/modal.vue'
import { client_id, getAppClientVersion, isDiwania, recall_value, scheme } from '.'
import ToModal from '../views/to-modal/index.vue'

/* eslint-disable import/prefer-default-export */
const targetVersoin = 10291
// const scheme = process.env.VUE_APP_FLAVOR === 'weyay' ? 'weyayplay' : 'sawaludo'

export function isTargetVersion() {
  if (getPlatform().isAndroid) {
    return true
  }
  try {
    const version = +getAppClientVersion()
    return version >= targetVersoin
  } catch (e) {
    return false
  }
}

export function isStatusStringVersion() {
  if (getPlatform().isAndroid) {
    return true
  }
  try {
    const version = +getAppClientVersion()
    return version >= 10449
  } catch (e) {
    return false
  }
}

type Params = {
  login_type: 'facebook' | 'google' | 'apple'
  user_id?: string // facebook、apple必填
  access_token?: string // facebook、apple必填
  id_token?: string // google必填
  appid?: string
  auth_type?: string // 1: ludo, 2: sawa, 3: apple, 4: diwania
  code?: string // 测试用
}

export async function callbackToClient(p: Params, status_string: string) {
  // if (p.login_type === 'apple') {
  //   p.appid = client_id.apple
  // } else if (p.login_type === 'google') {
  //   p.appid = client_id.google
  // } else if (p.login_type === 'facebook') {
  //   p.appid = client_id.facebook
  // }
  p.auth_type = isDiwania.value ? '4' : '1'
  const ticket = encodeURIComponent(JSON.stringify(p))
  const link = `${scheme}://sawaksa_login_key?ticket=${ticket}&is_recall=${recall_value}&source=${status_string}&status_string=${status_string}`

  if (p.login_type === 'apple') {
    await sleep(50)
  }
  // console.warn('deeplink>>', link)
  if (getArgsFromUrl('keep')) {
    return
  }

  const bundleId = getArgsFromUrl('bundle_id')
  if (bundleId.indexOf('com.badambiz.pk.saudi.jackaroo') >= 0) {
    showModal(ToModal, {
      cb: () => {
        location.href = link
      },
    })
  } else {
    location.href = link
  }
  // location.href = `./jump.html?jump_url=${link}`
}

export function goToMobileWebview(status_string: string) {
  const url = location.href.replace('#/', '#/mobile')
  try {
    location.href = `${scheme}://sawaksa_login_mobile?url=${encodeURIComponent(url)}&source=${status_string}&status_string=${status_string}`
  } catch (e) {
    //
  }
}

export async function getDeviceToken() {
  return new Promise((r) => {
    const id = registerBridgeCbId((res: any) => {
      try {
        const r1 = JSON.parse(res)
        if (r1 && r1.token) {
          r(r1.token)
        }
      } catch (e) {
        r('')
      }
    })
    const obj = { callback_id: id }
    console.log('getDeviceToken', obj)
    if (window.JavaBridgeObj && (window.JavaBridgeObj as any).getDeviceToken) {
      ; (window as any).JavaBridgeObj.getDeviceToken(JSON.stringify(obj))
    } else {
      r('')
    }
  })
}
